$typist-width: 693px;
$typist-height: 108px;

.designer-section-content {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.designer-section-typist-wrapper {
  width: $typist-width;
  height: $typist-height;
  white-space: nowrap;

  h3:not(:first-child) {
    margin-top: 24px;
  }
}

.typist-button-disabled {
  .link-button {
    cursor: initial;
  }
  .link-button:hover {
    background-color: initial;
  }
}

/* Animations */

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Copied from node_modules/react-typist/dist/Typist.css */

.Typist .Cursor {
  display: inline-block;
}
.Typist .Cursor--blinking {
  opacity: 1;
  animation: blink 1s linear infinite;
}

@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
